import React from 'react';
import './Card.css';

class Emoji extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
    this.props.updateEmoji();
  }


  render() {
    return (
      <div>
        <img
        src={this.props.emoji}
        className="img animated fadeIn"
        alt={this.props.emoji}
        />
      </div>
    )
  }
}

export default Emoji;

