//on this side, we want to show a fun little emoji!

//congrats on finishing [task]
//have a cute emoji to celebrate!


//a button to dismiss, hide the card forever ()

// eslint-disable-next-line
import React from 'react';
import './Card.css';
// import Congrats from './Congrats.js';
import Emoji from './Emoji.js';
import Uncheck from './Uncheck.js';
import Delete from './Delete.js';

class Backside extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  render() {
    return (
      <div>
        {this.props.show ?
          <div className="defaultCard">
            <div className="emojirow">
              <Emoji
              updateEmoji={this.props.updateEmoji}
              emoji={this.props.emoji}
              />
            </div>
            <div className="backiconsrow">
              <Uncheck
              unfinishTask={this.props.unfinishTask}
              />
              <Delete
              resetCard={this.props.resetCard}
              />
            </div>
          </div>
          :
          <div>
          </div>}
      </div>
    )
  }
};

//add functionality where, when you delete task forever, it goes away!
//add functionality where unfinish task means you change the state to not finished
export default Backside;
