import React from 'react';
import { Button } from 'evergreen-ui';
import './Card.css';

class Credit extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showcredit: false,
      clicked: false,
      dontshow: false
    }
  }

  toggle = () => {
    this.setState({ showcredit: !this.state.showcredit });
    if (!this.state.clicked) {
      this.setState({ clicked: true });
    }
    else {
      this.setState({ dontshow: true });
    }
  }

  render() {
    let button;
    if (!this.state.showcredit && !this.state.dontshow) {
      button =
      <div className="frontsiderow">
        <Button onClick={this.toggle}>Show credit</Button>
      </div>
    } else if (this.state.showcredit && !this.state.dontshow) {
      button =
      <div>
        <div className="frontsiderow">
          <Button onClick={this.toggle}>Hide credit</Button>
        </div>
        <div>Icons made by <a href="https://www.flaticon.com/authors/photo3idea-studio" title="photo3idea_studio">photo3idea_studio</a> from <a href="https://www.flaticon.com/"             title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/"             title="Creative Commons BY 3.0" target="_blank" rel="noopener noreferrer">CC 3.0 BY</a></div>
      </div>
    } else {
      button = <div></div>
    }
    return (
      <div>{button}</div>
    )
  }
};

export default Credit;
