import React from 'react';
import SimpleStorage from "react-simple-storage";
import './Card.css';
import Frontside from "./Frontside.js";
import Backside from "./Backside.js";
import { Icon } from 'evergreen-ui';
import Aromatherapy from './images/001-aromatherapy.png';
import Aroma from './images/002-aroma.png';
import Aroma2 from './images/004-aroma.png';
import Bamboo from './images/005-bamboo.png';
import Bathrobe from './images/006-bathrobe.png';
import Sauna from './images/007-sauna.png';
import Candle from './images/008-candle.png';
import Water from './images/012-water.png';
import Herb from './images/013-herb.png';
import Herb2 from './images/014-herb.png';
import Incense from './images/016-incense.png';
import LotusFlower from './images/017-lotus flower.png';
import Mortar from './images/022-mortar.png';
import Salt from './images/024-salt.png';
import Spa from './images/031-spa.png';
import Spa2 from './images/033-spa.png';
import Stone from './images/034-stone.png';
import Teapot from './images/035-teapot.png';
import Towels from './images/036-towels.png';

class Card extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      completed: false,
      task: "Task",
      show: true,
      showFront: false,
      emoji: null,
    }
  }

  updateTodo = (e) => {
    this.setState({ task: e.target.value });
  };

  finishTask = (e) => {
    e.preventDefault();
    this.setState({
      completed: !this.state.completed,
    });
  }

  unfinishTask = (e) => {
    e.preventDefault();
    this.setState({
      completed: !this.state.completed,
    });
  }

  resetCard = () => {
    this.setState({ task: "Task" });
    this.setState({ priority: "" });
    this.setState({ showFront: false });
    this.setState({ completed: false });
    this.setState({ emoji: null });
    this.props.resetPriority();
  };

  showFront = () => {
    this.setState({ showFront: true });
  };

  updateEmoji = () => {
    if (this.state.emoji === null) {
      this.setState({ emoji: this.zenImages[Math.floor(Math.random() * Math.floor(this.zenImages.length))]});
    }
  };

  icons = ["circle", "cloud", "code", "compass", "delta", "dot", "doughnut-chart", "flame",
  "full-circle", "git-commit", "graph", "heart", "intersection", "layer", "layout-circle",
  "layout-grid", "moon", "mountain", "ninja", "path", "ring", "selection", "small-plus",
  "snowflake", "tint", "tree", "unresolve"]

  colors = ["#425A70", "#1070CA", "#47B881", "#14B5D0", "#735DD0"];

  zenImages = [Aromatherapy, Aroma, Aroma2, Bamboo, Bathrobe, Sauna,
  Candle, Water, Herb, Herb2, Incense, LotusFlower, Mortar, Salt,
  Spa, Spa2, Stone, Teapot, Towels];


  render() {
    let iconDisplay;
    let frontDisplay;
    let backDisplay;
    if (!this.state.completed && !this.state.showFront) {
      iconDisplay = <Icon
      icon={this.icons[Math.floor(Math.random() * 27)]}
      color={this.colors[Math.floor(Math.random() * Math.floor(this.colors.length))]}
      onClick={this.showFront}
      className="icon"
      />
    }
    if (!this.state.completed && this.state.showFront) {
      frontDisplay = <Frontside
      task={this.state.task}
      updateTodo={this.updateTodo}
      priority={this.props.priority}
      updatePriority={this.props.updatePriority}
      finishTask={this.finishTask}
      updatePrioritytoLo={this.props.updatePrioritytoLo}
      updatePrioritytoMed={this.props.updatePrioritytoMed}
      updatePrioritytoHi={this.props.updatePrioritytoHi}
      // className={this.state.priority}
      className="Medium"
      />
    }
    if (this.state.completed) {
      backDisplay = <Backside
      unfinishTask={this.unfinishTask}
      task={this.state.task}
      resetCard={this.resetCard}
      deleteCard={this.deleteCard}
      deleteTodo={this.props.deleteTodo}
      show={this.state.show}
      updateEmoji={this.updateEmoji}
      emoji={this.state.emoji}
      />
    }

    return (
      <div className="container">
        <SimpleStorage parent={this} />
        {iconDisplay}
        {frontDisplay}
        {backDisplay}
      </div>
    )
  }
}

export default Card;

//colors i took out: "#EC4C47", "#D9822B", "#F7D154", (red,orange,yellow)
