import React from 'react';
import './Card.css';
import { Button } from 'evergreen-ui';

class Priority extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  render() {
    return (
      <div className="frontsiderow">
        <Button intent="success" onClick={this.props.updatePrioritytoLo}>Lo</Button>
        <Button intent="warning" onClick={this.props.updatePrioritytoMed}>Med</Button>
        <Button intent="danger" onClick={this.props.updatePrioritytoHi}>Hi</Button>
      </div>
    )
  }
}

export default Priority;



