// eslint-disable-next-line
import React from 'react';
import './Card.css';
import Todo from './Todo.js';
// import Date from './Date.js';
import Priority from './Priority.js';
import Completed from './Completed.js';

class Frontside extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  render() {
    return (
      <div className="defaultCard">
        <div className="frontsiderow taskrow">
          <Todo
          task={this.props.task}
          updateTodo={this.props.updateTodo}
          />
        </div>
        <div className="frontsiderow">
          <Priority
          priority={this.props.priority}
          updatePriority={this.props.updatePriority}
          updatePrioritytoLo={this.props.updatePrioritytoLo}
          updatePrioritytoMed={this.props.updatePrioritytoMed}
          updatePrioritytoHi={this.props.updatePrioritytoHi}
          />
        </div>
        <div className="frontsiderow completed">
          <Completed
          finishTask={this.props.finishTask}
          />
        </div>
      </div>
    )
  }
};

export default Frontside;

