import React from 'react';
import './Card.css';
import { TextInput } from 'evergreen-ui';

class Todo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  clearText = (e) => {
    if (e.target.value === "Task") {
      e.target.value = "";
    }
  }

  render() {
    return (
      <TextInput
      name="new-task"
      defaultValue={this.props.task}
      className="todoCard"
      width={120}
      onChange={this.props.updateTodo}
      onClick={this.clearText}
      />
    )
  }
}

export default Todo;

