import React from 'react';
import './Card.css';
import Card from './Card.js';
import SimpleStorage from "react-simple-storage";
class Cardbackground extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      priority: "defaultCard",
    }
  }

  updatePrioritytoLo = (e) => {
    e.preventDefault();
    if (this.state.priority !== "Lo") {
      this.setState({ priority: "Lo" });
    } else this.resetPriority();
  }

  updatePrioritytoMed = (e) => {
    e.preventDefault()
    if (this.state.priority !== "Med") {
      this.setState({ priority: "Med" });
    } else this.resetPriority();
  }

  updatePrioritytoHi = (e) => {
    e.preventDefault()
    if (this.state.priority !== "Hi") {
      this.setState({ priority: "Hi" });
    } else this.resetPriority();
  }

  resetPriority = () => {
    this.setState({ priority: "defaultCard" });
  }

    render() {
      return (
        <div className={this.state.priority}>
          <SimpleStorage parent={this} />
          <Card
          updatePrioritytoLo={this.updatePrioritytoLo}
          updatePrioritytoMed={this.updatePrioritytoMed}
          updatePrioritytoHi={this.updatePrioritytoHi}
          resetPriority={this.resetPriority}
          priority={this.state.priority}
          />
        </div>
      )
    }
  }

  export default Cardbackground;
